import React, {useEffect, useState} from 'react';
import {Progress, Typography} from 'antd';
import {getDay, getMonth, getNumberOfMonth, getPercent, geturrentTime} from '../utils/utils-clock';

const {Title, Text} = Typography;

export const Clock = () => {
    const [time, setTime] = useState<string>(geturrentTime())

    useEffect(() => {
        setInterval(() => setTime(geturrentTime()), 1000)
    }, [])

    return (
        <>
            <div className={'main'}>
                <Progress
                    type="circle"
                    strokeColor={{
                        '0%': '#00ffff',
                        '100%': '#0000ff',
                    }}
                    trailColor={'#e1e2e6'}
                    percent={getPercent()}
                    format={() => time}
                    width={280}
                    strokeWidth={12}
                />
                <div className={'main__content'}>
                    <Title level={5}>{getDay()}</Title>
                    <Text>{getNumberOfMonth()} {getMonth()}</Text>
                </div>
            </div>
        </>
    );
};

